import React, {Component} from "react";
import {connect} from "react-redux"

import {gotoCartSummary} from "eshop/modules/checkout/actions/app"
import {getCartEntries, getSelectedVoipNumber} from "../../selectors";
import CartEntryPreviewComponent from "./mobile/CartEntryPreviewComponent";
import BundleTypeEnum from "./BundleTypeEnum";
import {
    getPropositionListCount,
    getPropositionListCountSetMode,
    getSelectedOfferType,
    isB2B,
} from "eshop/modules/configurator/selectors/filters"
import SimCartIndexHeader from "eshop/modules/cart/components/entriesList/mobile/SimCartIndexHeader"
import InfoComponent from "eshop/components/InfoComponent";
import {getStayLoveRetentionMSISDN} from "eshop/modules/auth/selectors/authorization"
import OfferType from "../../../core/constants/OfferTypeEnum";

class MulticartEntriesListPreviewComponent extends Component {


    componentDidUpdate(prvProps) {
        if (this.props.entries && this.props.entries.length && prvProps.entries && this.props.entries.length !== prvProps.entries.length) {
            const that = this;
            setTimeout(function () {
                that.scrollToArchon()
            }, 500);
        }

    }

    scrollToArchon() {
        if (document.getElementById("afterAddToCartScrollAnchor")) {
            let headerHeigth = 0;
            if ($('.opl-header__main').length)
                headerHeigth = $('.opl-header__main').outerHeight();
            console.log("headerHeigth", headerHeigth);
            console.log(parseInt($("#afterAddToCartScrollAnchor").offset().top - headerHeigth));
            //      window.scrollTo({top: parseInt($("#afterAddToCartScrollAnchor").offset().top-headerHeigth),left: 0,behavior: 'smooth'});
            window.scrollTo(0, parseInt($("#afterAddToCartScrollAnchor").offset().top - headerHeigth));
        } else {

        }

    }

    render() {
        console.log("MulticartEntriesListPreviewComponent************************************* RENDER");

        const mobileEntries = this.props.entries && this.props.entries.filter(ent => {
            return ent.bundleType === BundleTypeEnum.VOICE || ent.bundleType === BundleTypeEnum.DATA
        });

        return (
            <div className="l-full-row g-gray1-bg">
                {!!mobileEntries && mobileEntries.map((entry, idx) => {
                    const bundleType = entry.bundleType;
                    const sectionConf = this.props.entryTitle[bundleType.toString()];
                    const bundleProps = {
                        descriptions: this.props.descriptions,
                        sectionConf,
                        entry,
                        idx,
                        channel: this.props.channels.sales,
                        sapReservation: this.props.reservationNumberSapPOS,
                        sapErrorMessage: this.props.descriptions.sapReservationError,
                        isB2B: this.props.isB2B,
                    };
                    switch (bundleType) {
                        case BundleTypeEnum.VOICE:
                            return <div key={idx + "_" + BundleTypeEnum.VOICE}
                                        id={(idx + 1 === mobileEntries.length) && "afterAddToCartScrollAnchor"}>
                                <CartEntryPreviewComponent {...bundleProps} /></div>;
                        case BundleTypeEnum.DATA:
                            return <div key={idx + "_" + BundleTypeEnum.DATA}
                                        id={(idx + 1 === mobileEntries.length) && "afterAddToCartScrollAnchor"}>
                                <CartEntryPreviewComponent {...bundleProps} /></div>;
                        default:
                            return null;
                    }
                })
                }
                <div className="l-page-row ">
                    {(!!mobileEntries && mobileEntries.length > 0) && <div className="">
                        <InfoComponent color={"grey"} bgColor={"grey"}>
                            Karta została dodana do koszyka, aby modyfikować zamówienie
                            <a href="#"
                               className="g-black1-c"
                               onClick={this.props.gotoCartSummary}> przejdź do koszyka</a>
                        </InfoComponent>
                    </div>}

                    <div id={(!mobileEntries || mobileEntries.length === 0) && "afterAddToCartScrollAnchor"}>
                        {this.props.propositionListCount > 0 && !this.props.isPropositionListCountSetMode && this.props.selectedOfferType !== OfferType.DATA &&
                        <div
                            className={"u-border-top u-no-margin u-padding-s u-padding-top-m"}>
                            <div className="o-icon-list u-padding-m">
                                <div className="o-icon-list__item">
                                    <div className="o-icon-list__icon u-large-hide u-medium-hide">
                                        <span className="g-icon g-icon--only g-icon--sim"/>
                                    </div>
                                    <div className="o-icon-list__text u-vertical-middle u-small-padding-top-s">
                                        {!(this.props.selectedOfferType === OfferType.VOICE_LDF) && <SimCartIndexHeader
                                            key="simCountCartBundleIndex"
                                            className=" u-small-no-margin"
                                            simIndex={!!mobileEntries ? mobileEntries.length : 0}/>}
                                    </div>
                                </div>
                            </div>

                        </div>
                        }
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    gotoCartSummary: () => dispatch(gotoCartSummary()),
});
const mapStateToProps = state => ({
    isPropositionListCountSetMode: getPropositionListCountSetMode(state),
    propositionListCount: getPropositionListCount(state),
    entries: getCartEntries(state),
    selectedNumberVoip: getSelectedVoipNumber(state),
    selectedOfferType: getSelectedOfferType(state),
    stayLoveRetentionMSISDN: getStayLoveRetentionMSISDN(state),
    isB2B:isB2B(state),
});

export default connect(mapStateToProps, mapDispatchToProps)(MulticartEntriesListPreviewComponent);


